import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import GithubSvg from "../svg/github";
import Help from "@material-ui/icons/Help";

import Dialog from "@material-ui/core/Dialog";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";

const bookSource = require("../mds/sourcerule");
const ReactMarkdown = require("react-markdown");

const styles = {
  root: {
    // position: "fixed",
    // width: "100%"
  },
  grow: {
    flexGrow: 1
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20
  },
  sourceRule: {
    width: "100%",
    paddingTop: "80px"
  },
  dialogToolbar: {
    position: "fixed",
    width: "100%"
  },
  rootToolbar: {
    position: "fixed",
    width: "100%"
  },
  mdRoot: {
    paddingLeft: "32px",
    paddingRight: "32px"
  }
};

function Transition(props) {
  return <Slide direction="up" {...props} />;
}

class MenuAppBar extends React.Component {
  state = {
    auth: true,
    anchorEl: null,
    dialogOpen: false
  };

  jumpUrl = url => {
    window.location.href = url;
  };

  openHelpDialog = () => {
    this.setState({ dialogOpen: true });
  };

  closeHelpDialog = () => {
    this.setState({ dialogOpen: false });
  };

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <AppBar position="static" className={classes.rootToolbar}>
          <Toolbar>
            <IconButton
              className={classes.menuButton}
              color="inherit"
              aria-label="Menu"
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit" className={classes.grow}>
              在线书源编辑
            </Typography>
            <IconButton color="inherit" onClick={() => this.openHelpDialog()}>
              <Help />
            </IconButton>
            <IconButton
              color="inherit"
              onClick={() => this.jumpUrl("https://github.com/zsakvo")}
            >
              <GithubSvg />
            </IconButton>
          </Toolbar>
        </AppBar>
        <Dialog
          fullScreen
          open={this.state.dialogOpen}
          onClose={this.handleClose}
          TransitionComponent={Transition}
        >
          <AppBar position="static" className={classes.dialogToolbar}>
            <Toolbar>
              <IconButton
                className={classes.menuButton}
                color="inherit"
                aria-label="Menu"
                onClick={this.closeHelpDialog}
              >
                <CloseIcon />
              </IconButton>
              <Typography variant="h6" color="inherit" className={classes.grow}>
                书源编写规则
              </Typography>
            </Toolbar>
          </AppBar>
          <div className={classes.mdRoot}>
            <ReactMarkdown source={bookSource} className={classes.sourceRule} />
          </div>
        </Dialog>
      </div>
    );
  }
}

MenuAppBar.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(MenuAppBar);
